import React, { useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import AcceptedMedicationModal from "../../MedicationModal/AcceptedForm";
import { sendAttachEmail } from "../../../../../../../app/features/patientPharmacy/patientPharmacy.slice";
import { useLocation, useNavigate } from "react-router-dom";
import { PATH } from "../../../../../../../utils/path";

export default function EmailPharmacyModal(props) {
  const { handleClose, show, medicinesList } = props;

  const { register, handleSubmit } = useForm();

  const [presModal, setPresModal] = useState({
    data: null,
    show: false,
  });
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { patientPharmacy } = useSelector((state) => state);
  console.log("patientPharmacy", patientPharmacy);
  const location = useLocation();
  const appointmentStatus = () => {
    navigate(PATH.DOCTOR_PRESCRIPTION_DASHBOARD);
  };
  const { getPatientData } = useSelector((state) => state?.patient);

  function sendPrescToPharmacy(data) {
    const finalData = {
      email: getPatientData?.pharmacyEmail
        ? getPatientData?.pharmacyEmail
        : data?.pharmacyEmail,
      appointmentId: null,
      patientFormStatusId: location?.state,
    };
    dispatch(sendAttachEmail({ finalData, appointmentStatus }));
  }

  return (
    <>
      <Modal
        show={show}
        onHide={handleClose}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className="appointment-modal"
        backdrop="static"
      >
        <Modal.Header
          closeButton
          className="font-weight-600"
          style={{ fontSize: "18px" }}
        ></Modal.Header>
        <Modal.Body>
          <div className="d-flex justify-content-center flex-column mx-5 mb-3">
            {medicinesList?.length <= 0 || medicinesList === undefined ? (
              <>
                <p
                  className="mb-0 font-weight-600 text-center"
                  style={{ fontSize: "20px" }}
                >
                  Please create Prescription to Complete the Online Prescription
                  Form
                </p>
                <div className="d-flex justify-content-center">
                  <Button
                    className="start-apt-btn d-flex border-0 px-5 mt-5"
                    onClick={() => {
                      setPresModal({ data: null, show: true });
                    }}
                  >
                    Create
                  </Button>
                </div>
              </>
            ) : (
              <>
                <Form onSubmit={handleSubmit(sendPrescToPharmacy)}>
                  <Form.Label
                    className="mb-4 font-weight-600"
                    style={{ fontSize: "20px" }}
                  >
                    Do you want to Email a Prescription to the Patient?
                  </Form.Label>
                  <div>
                    <p className="fw-bold m-0">Pharmcy Name:</p>
                    <p>{getPatientData?.pharmacyName ?? "N/A"}</p>
                  </div>
                  <div>
                    <p className="fw-bold m-0">Pharmcy Location:</p>
                    <p>{getPatientData?.pharmacyLocation ?? "N/A"}</p>
                  </div>
                  {getPatientData?.pharmacyEmail ? (
                    <div>
                      <p className="fw-bold m-0">Pharmcy Email:</p>
                      <p>{getPatientData?.pharmacyEmail ?? "N/A"}</p>
                    </div>
                  ) : (
                    <Form.Group >
                      <Form.Label className="fw-bold">Pharmacy Email:</Form.Label>
                      <Form.Control
                        type="email"
                        placeholder="Pharmacy Email"
                        {...register("pharmacyEmail")}
                        maxLength={250}
                      />
                    </Form.Group>
                  )}
                  <div className="mt-4 d-flex justify-content-center">
                    <Button
                      className="d-flex align-items-center me-4 font-weight-600 px-5"
                      onClick={appointmentStatus}
                      style={{
                        background: "transparent",
                        color: "#000071",
                        fontSize: "18px",
                        border: "1px solid #000071",
                      }}
                    >
                      <p className="mb-0">No</p>
                    </Button>
                    <Button
                      type="submit"
                      className="start-apt-btn d-flex align-items-center border-0 px-5"
                    >
                      Yes
                    </Button>
                  </div>
                </Form>
              </>
            )}
          </div>
        </Modal.Body>
      </Modal>
      {presModal && (
        <AcceptedMedicationModal
          show={presModal.show}
          onClose={() => setPresModal({ show: false, data: null })}
        />
      )}
    </>
  );
}
