import React, { useEffect, useState } from "react";
import { Button, Dropdown, Form } from "react-bootstrap";
import BootstrapTable from "react-bootstrap-table-next";
import ReactDOM from "react-dom";
import { AiFillEye } from "react-icons/ai";
import { BsThreeDotsVertical } from "react-icons/bs";
import { FaEdit } from "react-icons/fa";
import { RiDeleteBin5Fill } from "react-icons/ri";
import paginationFactory from "react-bootstrap-table2-paginator";
import { ConfirmationModal } from "../../../../../../components/Modal/ConfirmationModal";
import { useDispatch, useSelector } from "react-redux";
import {
  confirmOnlinePres,
  deletePatientPres,
  getOnlinePresMedicines,
} from "../../../../../../app/features/patientPrescription/patientPrescription.slice";
import AcceptedMedicationModal from "../MedicationModal/AcceptedForm";
import FullScreenLoader from "../../../../../../authorization/FullScreenLoader";
import { useLocation } from "react-router-dom";
import EmailPharmacyModal from "./EmailPharmacyModal";

function rowStyle() {
  return { backgroundColor: "#F8F8F8" };
}

export default function MedicationsDataTable() {
  const [presModal, setPresModal] = useState({
    data: null,
    show: false,
  });
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [showMedicine, setShowMedicine] = useState({ data: null, show: false });
  const [deleteId, setDeleteId] = useState();
  const [showEndModal, setShowEndModal] = useState(false);

  const dispatch = useDispatch();
  const { onlinePresMedicinesList, isLoading, isError, isSuccess } =
    useSelector((state) => state?.patientPrescription);

  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));
  const location = useLocation();

  useEffect(() => {
    const finalData = {
      patientFormStatusId: location?.state,
    };
    dispatch(getOnlinePresMedicines(finalData));
  }, [dispatch, location?.state]);

  let newVariable = [];

  if (onlinePresMedicinesList?.medicineList) {
    onlinePresMedicinesList?.medicineList?.forEach((data) => {
      if (data?.medicineList) {
        newVariable.push(...data.medicineList);
      }
    });
  }

  function onCreateSuccess() {
    const finalData = {
      patientFormStatusId: location?.state,
    };
    dispatch(getOnlinePresMedicines(finalData));
  }

  function deletePres() {
    const finalData = {
      medicineId: deleteId?.medicineId,
    };
    dispatch(deletePatientPres({ finalData, onCreateSuccess }));
  }

  function presToPatientFun(event) {
    const isPrescription = event.target.checked;
    const finalData = {
      prescriptionId: onlinePresMedicinesList?.prescriptionId,
      isConfirmed: true,
      showPrescription: isPrescription,
    };
    dispatch(confirmOnlinePres(finalData));
  }
  const columns = [
    {
      dataField: "medicineName",
      text: "Medicine Name",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Medicine Name</p>
          <p className="table-custom-data">{cell?.medicineName}</p>
        </>
      ),
    },
    {
      dataField: "doseFrequency",
      text: "Dose Frequency",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Dose Frequency</p>
          <p className="table-custom-data">{cell?.doseFrequency}</p>
        </>
      ),
    },
    {
      dataField: "dose",
      text: "Dose",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Dose</p>
          <p className="table-custom-data">{cell?.dose}</p>
        </>
      ),
    },
    {
      dataField: "formulation",
      text: "Formulation",
      headerAttrs: {
        hidden: true,
      },
      formatter: (row, cell) => (
        <>
          <p className="color-99 mb-0">Formulation</p>
          <p className="table-custom-data">{cell?.formulation}</p>
        </>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      formatter: (row, cell) => {
        return (
          <>
            <Dropdown>
              <Dropdown.Toggle
                id="dropdown-item-button1"
                size="sm"
                bsPrefix="moreIcon"
                className="moreIcon edit-dropdown border-0 bg-transparent"
                direction="left"
              >
                <BsThreeDotsVertical
                  className="float-right color-99"
                  size={25}
                />
              </Dropdown.Toggle>
              {ReactDOM.createPortal(
                <Dropdown.Menu>
                  <Dropdown.Item
                    as="button"
                    className="d-flex align-items-center"
                    onClick={() => setShowMedicine({ data: cell, show: true })}
                  >
                    <AiFillEye size={18} style={{ color: "#1A1A1A" }} />
                    <span
                      className="px-2"
                      style={{ fontSize: "14px", color: "#1A1A1A" }}
                    >
                      View
                    </span>
                  </Dropdown.Item>
                  <Dropdown.Item
                    as="button"
                    className="d-flex align-items-center"
                    onClick={() => setPresModal({ data: cell, show: true })}
                  >
                    <FaEdit size={18} style={{ color: "#1A1A1A" }} />
                    <span
                      className="px-2"
                      style={{ fontSize: "14px", color: "#1A1A1A" }}
                    >
                      Edit
                    </span>
                  </Dropdown.Item>
                  <Dropdown.Item
                    as="button"
                    className="d-flex align-items-center"
                    onClick={() => {
                      setDeleteId(cell);
                      setShowDeleteModal(true);
                    }}
                  >
                    <RiDeleteBin5Fill size={18} style={{ color: "#FD2121" }} />
                    <span
                      className="px-2"
                      style={{ fontSize: "14px", color: "#FD2121" }}
                    >
                      Delete
                    </span>
                  </Dropdown.Item>
                </Dropdown.Menu>,
                document.body
              )}
            </Dropdown>
          </>
        );
      },
      headerAttrs: {
        hidden: true,
      },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 6,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "6",
        value: 6,
      },
    ],
  };
  return (
    <div>
      <h5 className="px-2 py-2 fw-bold header-border">Prescription</h5>
      {isSuccess ? (
        onlinePresMedicinesList?.medicineList?.length <= 0 ||
        onlinePresMedicinesList?.medicineList === undefined ? (
          <div className="d-flex justify-content-center">
            <Button
              className="prescription-btn px-4 d-flex justify-content-center"
              onClick={() => setPresModal({ data: null, show: true })}
            >
              Create Prescription
            </Button>
          </div>
        ) : (
          <div className="custom-table-spacing">
            <Form.Check
              type="checkbox"
              id="show-prescription"
              label="Show Prescription to Patient"
              className="mt-3"
              onChange={(event) => presToPatientFun(event)}
            />
            <BootstrapTable
              keyField="medicineId"
              data={onlinePresMedicinesList?.medicineList}
              columns={columns}
              bordered={false}
              rowStyle={rowStyle}
              wrapperClasses="table-responsive"
              pagination={paginationFactory(paginateOptions)}
            />
            <div className="d-flex justify-content-end">
              <p
                className="text-cursor-pointer"
                style={{
                  color: "#2269F2",
                  fontWeght: 500,
                  fontSize: "18px",
                  cursor: "pointer",
                }}
                onClick={() => setPresModal({ data: null, show: true })}
              >
                + Add Prescription
              </p>
            </div>
            <Button
              variant="primary"
              onClick={() => setShowEndModal(true)}
              className="Admin-Add-btn fw-bold float-end"
            >
              Confirm
            </Button>
          </div>
        )
      ) : isLoading ? (
        <FullScreenLoader />
      ) : isError ? (
        <p className="text-center mt-3">Network Error...</p>
      ) : (
        <p className="text-center mt-3">No Record Found</p>
      )}
      <AcceptedMedicationModal
        data={presModal.data}
        show={presModal.show}
        view={showMedicine.show}
        viewData={showMedicine.data}
        onCloseView={() => setShowMedicine({ show: false, data: null })}
        onClose={() => setPresModal({ show: false, data: null })}
        slotDescription={slotDescription}
      />
      <ConfirmationModal
        show={showDeleteModal}
        onHide={() => setShowDeleteModal(false)}
        heading={"Delete Prescription"}
        title={"the prescription"}
        removeFunc={deletePres}
      />
      <EmailPharmacyModal
        show={showEndModal}
        handleClose={() => setShowEndModal(false)}
        medicinesList={onlinePresMedicinesList?.medicineList}
      />
    </div>
  );
}
